// src/app/login/page.tsx
'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Github } from 'lucide-react'
import { useAuth } from '@/hooks/use-auth' // <-- IMPORTANTE

// Defina o tipo User que será passado para o login (Deve ser consistente com AuthContext.ts)
interface UserData {
    id: number;
    username: string; 
    email: string;
}

export default function LoginPage() {
  const router = useRouter()
  const { login } = useAuth(); // Obtém a função login do contexto
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [error, setError] = useState('')
  const [loading, setLoading] = useState(false)

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setError('')
    setLoading(true)

    try {
      // 1. Requisição para o backend Django
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/auth/login/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      })

      const data = await response.json()

      if (response.ok) {
        const token = data.token;
        
        // **2. Etapa Faltante Crucial:** Obter dados do usuário
        // Neste exemplo, vamos apenas mockar os dados com base no email.
        // Em um app real, você faria um GET para '/api/auth/user/' usando o token
        const userData: UserData = { id: 1, username: email, email: email };
        
        // 3. Usa a função 'login' do contexto para salvar o token e redirecionar
        login(token, userData) 
        
      } else {
        setError(data.message || 'Credenciais inválidas')
      }
    } catch (err) {
      setError('Erro ao conectar com o servidor')
    } finally {
      setLoading(false)
    }
  }

  // ... restante do componente LoginPage (handleSocialLogin, JSX) ...
  // Inclua a renderização do seu JSX fornecido
  // ...
  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      {/* <header className="bg-black text-white py-4 px-8">
        <nav className="flex items-center justify-between max-w-7xl mx-auto">
          <div className="font-bold text-2xl tracking-tight font-mono">Dostanza</div>
          <div className="hidden md:flex items-center gap-8 text-sm">
            <Link href="/explore" className="hover:opacity-80">Explore</Link>
            <Link href="/progress" className="hover:opacity-80">Progresso</Link>
            <Link href="/ranking" className="hover:opacity-80">Ranking</Link>
            <Link href="/community" className="hover:opacity-80">Comunidade</Link>
            <Link href="/achievements" className="hover:opacity-80">Donquistas</Link>
          </div>
          <button className="md:hidden">
            <div className="w-6 h-0.5 bg-white mb-1.5"></div>
            <div className="w-6 h-0.5 bg-white mb-1.5"></div>
            <div className="w-6 h-0.5 bg-white"></div>
          </button>
        </nav>
      </header> */}

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-8 py-16">
        <div className="grid md:grid-cols-2 gap-16 items-center">
          {/* Left Side - Decorative */}
          <div>
            <h1 className="text-5xl font-bold mb-4 text-black">
              Como se estivesse
            </h1>
            <h2 className="text-5xl font-bold text-[#d946ef]">Jogando</h2>
            
            <div className="relative mt-16 h-96">
              {/* Transparent purple background rectangle */}
              <div className="absolute left-0 top-16 w-80 h-80 bg-purple-500/5 rounded-3xl"></div>
              
              {/* Purple island - top left */}
              <div className="absolute left-8 top-24 w-28 h-28 bg-gradient-to-br from-[#a855f7] to-[#9333ea] rounded-3xl shadow-xl z-10"></div>
              
              {/* Pink island - top right */}
              <div className="absolute left-52 top-8 w-24 h-24 bg-gradient-to-br from-[#ec4899] to-[#db2777] rounded-3xl shadow-xl z-10"></div>
              
              {/* Coral island - bottom center */}
              <div className="absolute left-32 bottom-8 w-28 h-28 bg-gradient-to-br from-[#f87171] to-[#ef4444] rounded-3xl shadow-xl z-10"></div>
              
              {/* Dashed connection lines between islands */}
              <svg className="absolute inset-0 w-full h-full pointer-events-none z-10" style={{ overflow: 'visible' }}>
                {/* Line from purple to pink island */}
                <path 
                  d="M 70 150 Q 120 90 220 70" 
                  stroke="#94a3b8" 
                  strokeWidth="2.5" 
                  strokeDasharray="8,6" 
                  strokeLinecap="round"
                  fill="none" 
                />
                {/* Line from purple to coral island */}
                <path 
                  d="M 70 180 Q 100 240 160 300" 
                  stroke="#94a3b8" 
                  strokeWidth="2.5" 
                  strokeDasharray="8,6"
                  strokeLinecap="round"
                  fill="none" 
                />
              </svg>
            </div>
          </div>

          {/* Right Side - Login Form */}
          <div className="bg-gray-50 rounded-3xl p-8 shadow-sm">
            <div className="mb-8">
              <h3 className="text-3xl font-bold font-pixel">
                <span className="bg-gradient-to-r from-[#d946ef] via-[#ec4899] to-[#a855f7] bg-clip-text text-transparent">
                  Costanza
                </span>
                {' '}<span className="text-gray-800">x</span>{' '}
                <span className="bg-gradient-to-r from-[#f97316] via-[#fb923c] to-[#f87171] bg-clip-text text-transparent">
                  UniAp
                </span>
              </h3>
            </div>

            <form onSubmit={handleLogin} className="space-y-6">
              <div>
                <Label htmlFor="email" className="text-black font-medium">Email</Label>
                <Input
                  id="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="mt-2 bg-white border-gray-200 h-12 rounded-xl"
                  required
                />
              </div>

              <div>
                <div className="flex items-center justify-between mb-2">
                  <Label htmlFor="password" className="text-black font-medium">Senha</Label>
                  <Link href="/forgot-password" className="text-sm text-gray-500 hover:text-gray-700">
                    Esqueceu a senha?
                  </Link>
                </div>
                <Input
                  id="password"
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="bg-white border-gray-200 h-12 rounded-xl"
                  required
                />
              </div>

              {error && (
                <div className="text-red-500 text-sm bg-red-50 p-3 rounded-lg">
                  {error}
                </div>
              )}

              <Button
                type="submit"
                disabled={loading}
                className="w-full h-12 bg-black text-white hover:bg-gray-800 rounded-xl font-medium"
              >
                {loading ? 'Entrando...' : 'Entrar'}
              </Button>

              <div className="relative">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-gray-300"></div>
                </div>
                <div className="relative flex justify-center text-sm">
                  <span className="px-4 bg-gray-50 text-gray-500">Ou continue com</span>
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                {/* Botões de Social Login */}
                <Button
                  type="button"
                  variant="outline"
                  // Implemente handleSocialLogin se o Django suportar OAuth
                  className="h-12 border-gray-200 hover:bg-gray-100 rounded-xl"
                >
                  {/* Google SVG ... */}
                  Google
                </Button>
                <Button
                  type="button"
                  variant="outline"
                  // Implemente handleSocialLogin se o Django suportar OAuth
                  className="h-12 border-gray-200 hover:bg-gray-100 rounded-xl"
                >
                  <Github className="w-5 h-5 mr-2" />
                  GitHub
                </Button>
              </div>

              <p className="text-center text-sm text-gray-600">
                Não tem uma conta?{' '}
                <Link href="/register" className="text-[#d946ef] font-medium hover:underline">
                  Cadastre-se
                </Link>
              </p>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}